@import url(https://fonts.googleapis.com/css2?family=Zen+Antique+Soft&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
}

body {
  font-family: "Zen Antique Soft", serif;
}

/************************************** NAVBAR **************************************/

/**** NAV LINKS ****/

nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.038rem;
  cursor: pointer;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.275rem solid, rgba(255, 255, 255, 0);
  border-top: 0.275rem solid, var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active:focus {
  border-top: 0.275rem solid #f9ab00;
  border-top: 0.275rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.275rem solid #f9ab00;
  border-top: 0.275rem solid var(--primary-orange);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

